@import "styles/colors.scss";

.tooltip-inner {
  background-color: $color-foreground;
  color: $color-background;
}

#skills {
  .low-padding-cols > div {
    padding: 5px;
  }
  .progress {
    height: inherit;
  }
  .progress-bar {
    background-image: initial;
    height: 2.5rem;
  }
  .progressTitle {
    margin-bottom: 2px;
  }
  .progressTitleWrapper {
    display: flex;
    span {
      float: left;
      padding-left: 0.3rem;
    }
    span:first-child{
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    
  }
  .skill-item {
    font-size: 1.1rem;
    text-align: left;
  }
  .skill-weight-1 {
    font-weight: bold;
    font-size: 1.2rem;
  }
  .skill-name {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .text {
    transition: transform .2s; /* Animation */
    width: 200px;
    height: 200px;
    margin: 0 auto;
    .skill-item:hover {
      transform: scale(2.5);
      color: $color-background;
      position: relative;
      left: 25%;
      border-radius: 5px;
      font-weight: unset;
      padding: 0.2rem;
      background-color: $color-title;
      text-align: left;
    }
  }
}

